  <section>
    <div class="select-box">
      <span></span>
      <section>
        <div v-for="(t,index) in tests" class="icon-box">
          <button type="text" :title="t.time" @click="select_index = index">
            <i class="iconfont icon-circle" :class="[t.type , select_index == index ?'active' : '']"></i>
          </button>
          <br>
          <span>{{t.time}}</span>
        </div>
      </section>
      <p>
        <span><i class="iconfont icon-circle a"></i>A</span>
        <span><i class="iconfont icon-circle b"></i>B</span>
        <span><i class="iconfont icon-circle c"></i>C</span>
        <span><i class="iconfont icon-circle d"></i>D</span>
      </p>
    </div>
    <div class="box">
      <header>
        <span></span>
      </header>
      <section>
        <table>
          <tr>
            <td class="desc"></td>
            <td colspan="5"></td>
          </tr>
          <tr>
            <td class="desc"></td>
            <td></td>
            <td class="desc"></td>
            <td></td>
            <td class="desc"></td>
            <td>{{tests[select_index].time}}</td>
          </tr>
          <tr>
            <td class="desc"></td>
            <td>500kV</td>
            <td class="desc"></td>
            <td>500kV</td>
            <td class="desc"></td>
            <td>PD Check</td>
