          <tr>
            <td class="desc"></td>
            <td colspan="5">0.0002V</td>
          </tr>
          <tr>
            <td class="desc" rowspan="2"></td>
            <td class="desc"></td>
            <td class="desc" colspan="4">pC</td>
          </tr>
          <tr>
            <td>{{tests[select_index].time}}</td>
            <td colspan="4">0.2V</td>
