          <tr>
            <td colspan="2" class="desc"></td>
          </tr>
          <tr>
            <td colspan="2"></td>
          </tr>
          <tr>
            <td colspan="2" class="desc"></td>
          </tr>
          <tr>
            <td> </td>
            <td> </td>
          </tr>
        </table>
      </section>
    </div>
    <div class="box">
      <header>
        <span></span>
      </header>
      <section>
        <table>
          <tr>
            <td class="desc"></td>
            <td colspan="5"></td>
          </tr>
          <tr>
            <td class="desc"></td>
            <td></td>
            <td class="desc"></td>
            <td></td>
            <td class="desc"></td>
            <td>{{tests[select_index].time}}</td>
          </tr>
          <tr>
            <td class="desc"></td>
            <td>500kV</td>
            <td class="desc"></td>
            <td>500kV</td>
            <td class="desc"></td>
            <td>Flir</td>
          </tr>
          <tr>
            <td class="desc"></td>
            <td colspan="5"></td>
          </tr>
        </table>
        <div class="wave">
          <span></span>
          <section>
            <div v-for="i in 4">
              <img src="/static/img/IR1.jpg" :key="i">
              <br>
              <span>A-{{tests[select_index].time}}</span>
            </div>
          </section>
        </div>
        <table>
          <tr>
            <td colspan="2" class="desc"></td>
          </tr>
          <tr>
            <td colspan="2"></td>
          </tr>
          <tr>
            <td colspan="2" class="desc"></td>
          </tr>
          <tr>
            <td colspan="2"></td>
          </tr>
          <tr>
            <td colspan="2" class="desc"></td>
          </tr>
          <tr>
            <td> </td>
            <td> </td>
          </tr>
        </table>
      </section>
    </div>
  </section>
</template>
<script>
export default {
  data() {
    return {
      tests: [{
        time: '2018-01-13',
        type: 'd'
      }, {
        time: '2018-02-15',
        type: 'd'
      }, {
        time: '2018-03-13',
        type: 'b'
      }, {
        time: '2018-03-27',
        type: 'd'
      }, {
        time: '2018-04-13',
        type: 'c'
      }, {
        time: '2018-05-12',
        type: 'd'
      }, {
        time: '2018-05-26',
        type: 'd'
      }, {
        time: '2018-06-10',
        type: 'd'
      }, {
        time: '2018-06-23',
        type: 'd'
      }],
