<style scoped>
.select-box {
  padding-left: 15px;
  color: #3c3c3c;
  font-size: 14px;
  line-height: 40px;
  height: 80px;
  position: relative;
  margin-bottom: 15px;
}

.select-box>section {
  background-color: #3c3c3c;
  height: 2px;
  position: absolute;
  left: 100px;
  top: 20px;
  right: 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.icon-box{
  position: relative;
}
.icon-box span{
  position: absolute;
  top: 30px;
  left: -30px;
  width: 200px;
  line-height: 16px;
}
.box {
  margin: 0 10px;
  border: 1px solid #9c9c9c;
  /*border-top: none;*/
  color: #3c3c3c;
}

.box>header {
  height: 35px;
  text-align: left;
  line-height: 35px;
  font-size: 16px;
  padding-left: 10px;
  border-bottom: 1px solid #9c9c9c;
  background-color: #dfecec;
  color: #306C95;
}

.box>section {
  padding: 0;
}

table {
  width: 100%;
  border: none;
  border: 1px solid #9c9c9c;
  border-collapse: collapse;
  text-align: center;
  margin: 0;
  overflow: auto;
  line-height: 30px;
  font-size: 14px;
}

td,
th {
  border: 1px solid #9c9c9c;
  text-align: left;
  padding-left: 10px;
}

td.desc {
  background-color: #ddd;
  /*text-align: center;*/
}

.wave {
  width: 100%;
  margin-bottom: 5px;
  line-height: 30px;
}

.wave>span {
  padding-left: 10px;
  font-size: 14px;
}

.wave>section {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.wave>section>div {
  width: 22%;
  text-align: center;
  font-size: 14px;
}

.wave img {
  width: 100%;
}

.a {
  color: red;
}

.b {
  color: orange;
}

.c {
  color: yellow;
}

.d {
  color: green;
}

.active {
  animation: blink 1000ms infinite;
}


@keyframes blink {
  from {
    opacity: 1.0;
  }
  50% {
    opacity: 0.2;
  }
  to {
    opacity: 1.0;
  }
}

p {
  padding-left: 80px;
  padding-top: 5px;
}

p span {
  display: inline-block;
  margin-right: 20px;
}

</style>



